import React from "react";
import check_token from "../../utils.js/check_toke";
const axios = require('axios');
class AddCar extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			type_car:'',
            price:'',
            car_number:'',
            worker:'',
		};
		this.onChange=this.onChange.bind(this);
		this.handleSubmit=this.handleSubmit.bind(this);
	}


	componentDidMount() 
	{
		check_token();
	}
	handleSubmit(event)
	{
		axios.post('http://localhost:8000/api/AddCar/',
        {
            'type_car':this.state.type_car,
            'price':this.state.price,
            'car_number':this.state.car_number,
        })
	}
	onChange = (e) => {
		this.setState({[e.target.name]:e.target.value})
		if (e.target.name==='type_car')
		{
			axios.post('http://localhost:8000/api/get_car_price',
			{'type_car':e.target.value})
			.then(res=>
			{
				this.setState({price:res.data.price})
			})
		}
	}

	render() {

		return (
			<div className="row">
				<div className="col-sm-4"></div>
				<div className="col-sm-4 bg-ligth text-black ">
					<table className="table">
						<tbody>
						<tr>
							<td> </td>
							<td>
                                <select style={{width:'400px'}} name='type_car' value={this.state.type_car} onChange={this.onChange}>
                                    <option value=""></option>
                                    <option value='Tent low 3 meters'>  3 </option>
                                    <option value='Van 3 meters'> 3 </option>
                                    <option value='Van high 3 meters'>  3 </option>
                                    <option value='Tent low 4 meters'>  4 </option>
                                    <option value='Van 4 meters'> 4 </option>
                                    <option value='Van high 4 meters'>  4 </option>
                                    <option value='Heel'></option>
                                    <option value='Tent 3 tons'> 3 </option>
                                    <option value='Van 3 tons'> 3 </option>
                                    <option value='Board 3 tons'> 3 </option>
                                    <option value='Refrigerator 3 tons'> 3 </option>
                                    <option value='Board 5 meters 5 tons'> 5  5 </option>
                                    <option value='Van 5 meters 5 tons'> 5  5</option>
                                    <option value='Tent 5 meters 5 tons'> 5  5 </option>
                                    <option value='Refrigerator 5 meters 5 tons'> 5  5 </option>
                                    <option value='Tent 6 meters 10 tons'> 6  10 </option>
                                    <option value='Van 6 meters 10 tons'> 6  10 </option>
                                    <option value='Board 6 meters 10 tons'> 6  10 </option>
                                    <option value='Refrigerator 6 meters 10 tons'> 6  10 </option>
                                    <option value='Tent 20 tons'> 20 </option>
                                    <option value='Van 20 tons'> 20 </option>
                                    <option value='Board 20 tons'> 20 </option>
                                    <option value='Refrigerator 20 tons'> 20 </option>
                                </select>
                            </td>
						</tr>
						<tr>
							<td></td>
							<td>{this.state.price}</td>
						</tr>
						<tr>
							<td> </td>
							<td><input style={{width:'400px'}} type="text" name='car_number' value={this.state.car_number} onChange={this.onChange}/></td>
						</tr>
						</tbody>
					</table>
					<center>
						<input type="button" style={{width:'200px'}} value="!" onClick={this.handleSubmit}/>
					</center>
				</div>
			</div>
		);

	}
};
export default AddCar;